import { FETCH_WEATHER } from "./types";

export const fetchWeather = () => async (dispatch) => {
  const ids = {
    Dallas: 4684888,
    Montréal: 6077243,
    Tokyo: 1850147,
  };

  const fetches = await Promise.all(
    Object.values(ids).map((e) =>
      fetch(
        `https://api.openweathermap.org/data/2.5/forecast?id=${e}&appid=50e626eada61d5b3ab891c2636a8e8fb` // here you put your token key
      ).then((e) => e.json())
    )
  );

  dispatch({
    type: FETCH_WEATHER,
    payload: {
      // iterating through object does not guarantee order, so I chose manually
      Dallas: fetches[0],
      Montréal: fetches[1],
      Tokyo: fetches[2],
    },
  });
};
